// Default style based on theme colors:
.alert-default {
    --bs-alert-color: var(--color-primary-text);
    --bs-alert-bg: var(--color-emphasize);
    --bs-alert-border-color: var(--color-emphasize);
    --bs-alert-link-color: var(--color-primary-text);
    i {
        font-size: 32px;
        max-width: 32px;
    }
}

