// Responsive iframe feature source: https://www.w3schools.com/howto/howto_css_responsive_iframes.asp
// Check youtube.html file for the implementation.

.content-video {
  position: relative;
  width: 100%;
  overflow: hidden;
}

// Use one of the following .content-video-x-y classes with .content-video class in same container
// element to setup the video aspect ratio for resizing video iframe behavior:
.content-video-3-2 {
  padding-top: 66.66%; /* 3:2 Aspect Ratio */
}
.content-video-4-3 {
  padding-top: 75%; /* 4:3 Aspect Ratio */
}
.content-video-16-9 {
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
}

.content-video-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: none;
}
