.page-categories {
  .content {
    text-align: justify;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    strong {
      color: var(--color-emphasize);
    }
    span {
      color: var(--color-base-text-2);
    }
    a {
      text-decoration: none;
      &:hover {
        opacity: 0.8;
        text-decoration: underline;
        text-decoration-color: var(--color-emphasize);
      }
    }
  }
}
