.card-project {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0;
  background-color: var(--color-card-bg);
  overflow: hidden;
  position: relative;
  border: none;
  .card-thumbnail {
    border-radius: $card-border-radius;
    overflow: hidden;
    a {
      display: block;
    }
    img {
      aspect-ratio: 16 / 9;
      width: 100%;
      object-fit: contain;
      &:hover {
        opacity: 0.8;
      }
    }
    &.card-thumbnail-large {
      @include media-breakpoint-up(md) {
        max-height: 260px;
      }
      @include media-breakpoint-up(xl) {
        max-height: 300px;
      }
    }
  }
  .card-title {
    h2,
    h3 {
      font-weight: 500;
      font-size: 20px;
      letter-spacing: -0.2px;
    }
    a {
      color: var(--color-emphasize);
    }
  }
  .card-description {
    margin: 2px 0 5px 0;
    font-size: 14px;
    line-height: 1.3;
    color: var(--color-base-text-2);
    margin: 0;
    font-weight: normal;
  }
  .card-title + .card-description {
    margin-top: 0;
  }
  .card-client {
    font-weight: 300;
    font-size: 14px;
    color: var(--color-base-text-2);
  }
  .card-content {
    padding-top: 6px;
    display: flex;
    flex: 1;
    flex-direction: column;
  }
  .card-readmore {
    margin-top: auto;
    line-height: 1;
    i {
      margin-left: 5px;
      margin-top: 4px;
    }
  }
  .card-date {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-weight: normal;
    color: var(--color-base-text-2);
    font-weight: normal;
    font-size: 16px;
  }
  .card-categories {
    display: inline-flex;
    font-size: 12px;
    font-family: $font-family-monospace;
    > a {
      background-color: var(--color-base-bg-2);
      padding: 3px 7px;
      border-radius: 2px;
      margin-right: 10px;
    }
  }

  .project-studio {
    font-size: 16px;
    color: var(--color-base-text);
    strong {
      display: inline-block;
      font-weight: 600;
      width: 120px;
    }
    span {
      display: inline-block;
      font-weight: 400;
    }
  }

  .project-categories {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: left;
    flex-wrap: wrap;
    column-gap: 7px;
    .category-link {
      margin-top: -10px;
      margin-right: 10px;
    }
    a {
      color: var(--color-base-text);
      font-size: 12px;
      font-weight: 600;
    }
  }

  // Tweaks for dark mode
  html[data-mode="dark"] & {
  }
}
