@import "../../framework/zerostatic/blocks/components/_gallery-video.scss";

.page-post {
  .title {
    h1 {
      font-weight: 500;
    }
  }
  .categories {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: left;
    flex-wrap: wrap;
    column-gap: 7px;
    .category-link {
      margin-bottom: 5px;
      margin-right: 7px;
    }
    a {
      font-size: 15px;
      background: var(--color-emphasize);
      color: var(--color-primary-text);
      display: inline-flex;
      padding: 2px 8px;
      border-radius: $card-border-radius;
      height: 26px;
      &:hover {
        opacity: 0.8;
        text-decoration: none;
      }
    }
  }
  .content {
    text-align: justify;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    strong {
      color: var(--color-emphasize);
    }
    // This add bottom margin to all div when contain an iframe (e.g. Youtube shortcut)
    div:has(iframe) {
      margin-bottom: 40px;
    }
  }
}
