@import "../../framework/zerostatic/blocks/components/_gallery-video.scss";

.page-project {
  .project-heading {
    h1 {
      font-size: 36px;
      letter-spacing: -2px;
      line-height: 1.2;
      font-weight: 700;
      margin-bottom: 10px;
      color: var(--color-emphasize);
      @include media-breakpoint-up(md) {
        font-size: 48px;
      }
    }
  }
  .project-categories {
    margin-bottom: 40px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: left;
    flex-wrap: wrap;
    column-gap: 15px;
    row-gap: 3px;
    a {
      color: var(--color-base-text);
      font-weight: 600;
    }
  }
  .project-description {
    width: 100%;
    p {
    }
  }
  .gallery {
    display: grid;
    gap: $grid-gutter-width;
    .gallery-item {
      .gallery-image {
        img {
          vertical-align: bottom;
        }
      }
      .gallery-video {
        video {
          vertical-align: bottom;
          width: 100%;
          height: auto;
        }
      }
      .gallery-caption {
        margin-top: 5px;
        font-size: 14px;
        strong {
          font-weight: 600;
        }
        span {
          color: var(--color-base-text-2);
          font-weight: 400;
        }
      }
    }
  }
  .gallery-single-column {
    display: grid;
    gap: $grid-gutter-width;
    grid-template-columns: 1fr;
  }
  .gallery-two-column {
    display: grid;
    gap: $grid-gutter-width;
    grid-template-columns: 1fr;
    @include media-breakpoint-up(md) {
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 300px;
    }
    @include media-breakpoint-up(md) {
      grid-auto-rows: 400px;
    }

    .gallery-item {
      overflow: hidden;
    }
  }
  .gallery-masonry {
    margin-left: calc(#{$grid-gutter-width} / 2 * -1);
    margin-right: calc(#{$grid-gutter-width} / 2 * -1);
    display: flex;

    .gallery-sizer {
      width: 100%;
      @include media-breakpoint-up(md) {
        width: 50%;
      }
    }
    .gallery-item {
      padding: 15px;
      width: 100%;
      @include media-breakpoint-up(md) {
        width: 50%;
      }
    }
  }
  .content {
    width: 90%;
    text-align: justify;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    strong {
      color: var(--color-emphasize);
    }
  }
  .project-image {
    img {
      max-width: 100%;
      height: auto;
    }
  }
  .project-stats {
    margin-top: 10px;
    margin-bottom: 40px;
    strong {
      color: var(--color-emphasize);
    }
  }
  .project-date,
  .project-role,
  .project-studio,
  .project-technologies,
  .project-platforms,
  .project-genre,
  .project-download,
  .project-sourcecode,
  .project-links {
    margin-bottom: 10px;
    font-size: 16px;
    strong {
      display: inline-block;
      font-weight: 600;
      width: 120px;
    }
    span {
      display: inline-block;
      font-weight: 400;
    }
    a {
      color: var(--color-base-text);
      text-decoration: underline;
      text-decoration-thickness: 1px;
      &:hover {
        color: var(--color-base-text);
        text-decoration-thickness: 2px;
      }
    }
    li {
      list-style-type: circle;
      list-style-position: inside;
      &:hover {
        list-style-type: disc;
      }
    }
  }
}
