.gallery {
  display: grid;
  gap: $grid-gutter-width;
  .gallery-item {
    .gallery-image {
      img {
        vertical-align: bottom;
        max-width: 100%;
        height: auto;
      }
    }
    .gallery-video {
      video {
        vertical-align: bottom;
        width: 100%;
        height: auto;
      }
    }
    .gallery-caption {
      margin-top: 5px;
      font-size: 14px;
      strong {
        font-weight: 600;
      }
      span {
        color: var(--color-base-text-2);
        font-weight: 400;
      }
    }
  }
}
.gallery-masonry {
  margin-left: calc(#{$grid-gutter-width} / 2 * -1);
  margin-right: calc(#{$grid-gutter-width} / 2 * -1);
  display: flex;

  .gallery-sizer {
    width: 100%;
    @include media-breakpoint-up(md) {
      width: 50%;
    }
  }
  .gallery-item {
    padding: 15px;
    width: 100%;
    @include media-breakpoint-up(md) {
      width: 50%;
    }
  }
}
