// Style for "dress" the jekyll-twitter-plugin error output.
// Replicated the Twitter light style from the embed tweet CSS code.
.jekyll-twitter-plugin:has(p) {
  background-color: white;
  border-width: 1px;
  border-radius: 12px;
  border-color: rgb(207, 217, 222);
  border-style: solid;
  padding-top: 12px;
  padding-right: 16px;
  padding-left: 16px;
  padding-bottom: 4px;
  margin-bottom: 40px;
  p {
    text-align: start;
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
    font-size: 20px;
    font-weight: 400;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  // Twitter-X logo:
  p::before {
    content: "\F8DB";
    color: black;
    font-family: bootstrap-icons;
    font-size: 24px;
    font-weight: 100;
    padding-right: 12px;
  }
  // Style for the first line, as title:
  p::first-line {
    color: rgb(0, 111, 214);
    font-size: 24px;
    font-weight: 600;
  }
}
