// Lightmode colors
$primary: #0d042b;
$primary-2: #0d042b;
$primary-text: #fdfcf9;
$base-bg: #fdfcf9;
$base-bg-2: #f7f6ed;
$base-bg-3: #faebd7;
$base-text: #0d042b;
$base-text-2: #3b364c;
$emphasize: #1e73be;
$logo-text:  #1e73be;

// Darkmode colors
$primary-dark: #f5f5ec;
$primary-2-dark: #f5f5ec;
$primary-text-dark: #fdfcf9;
$base-bg-dark: #202020;
$base-bg-2-dark: #303030;
$base-bg-3-dark: #404040;
$base-text-dark: #f5f5ec;
$base-text-2-dark: #929eaf;
$emphasize-dark: #dd8500;
$logo-text-dark: #dd8500;

$zs-colors-light: (
    "primary": $primary,
    "primary-2": $primary-2,
    "primary-text": $primary-text,
    "base-bg": $base-bg,
    "base-bg-2": $base-bg-2,
    "base-bg-3": $base-bg-3,
    "base-text": $base-text,
    "base-text-2": $base-text-2,
    "emphasize": $emphasize,
    "logo-text": $logo-text
);

$zs-colors-dark: (
    "primary": $primary-dark,
    "primary-2": $primary-2-dark,
    "primary-text": $primary-text-dark,
    "base-bg": $base-bg-dark,
    "base-bg-2": $base-bg-2-dark,
    "base-bg-3": $base-bg-3-dark,
    "base-text": $base-text-dark,
    "base-text-2": $base-text-2-dark,
    "emphasize": $emphasize-dark,
    "logo-text": $logo-text-dark
);

$zs-colors-light-css-vars: ();
@each $color, $value in $zs-colors-light {
  $zs-colors-light-css-vars: map-merge(
    $zs-colors-light-css-vars,
    (#{$color}: var(--color-#{$color}))
  )
}

$font-family-heading: DM Sans, -apple-system, serif;
$font-family-base: DM Sans, -apple-system, "Helvetica Neue", Arial, sans-serif; 
$font-family-monospace: Fira Mono, SFMono-Regular, Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-logo: DM Sans, -apple-system, "Helvetica Neue", Arial, sans-serif;

:root{  
  @each $color, $value in $zs-colors-light {
    --color-#{$color}: #{$value};
  }
}

html[data-mode='dark'] {
  @each $color, $value in $zs-colors-dark {
      --color-#{$color}: #{$value};
  }
}

@import 'style';
